html, body {
    width: 100%;
    height:100%;
    color: white;
    font-family: 'Courier New', Courier, monospace;
}

body {
    background: linear-gradient(-90deg, #58427C, #243B55,#243B55, #4E4376 );
    background-size: 400% 400%;
    -webkit-animation: gradient 10s ease infinite;
            animation: gradient 10s ease infinite;
}

@-webkit-keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
.navbar {
    font-size: 18px;
}

.About {
    display: flex;
    position: relative;
    padding-top: 45px;
}

.about-desc {
    text-align: center;
    padding: 100px 0;
}

.avatar {
    max-width:100%;
    height: auto;
}

.about-heading {
    font-size: 55px;
}

.about-resume {
    font-size: 16px;
}

.home-header {
    text-align: center;
    font-size: 2.4vw;
    position: absolute;
    top: 45%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.home-icons {
    position: absolute;
    top: 60%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color:#32174D;
}

.projects-icons{
    color:#32174D;
}

.Projects {
    padding-top: 45px;
}

a:link { 
    text-decoration: none; 
    padding: 15px;
    color: #32174D;
}

.linkedin-link, .github-link, .email-link {
    color: #32174D;
}

a:hover {
   color: white;
}

.Connect {
    padding: 45px;
}

.send-button {
    width: 100%;
    padding: 10px;
    border: 0;
    cursor: pointer;
    background: #E08D3C;
    font-size: 18px;
    margin-bottom: 15px;
    text-transform: uppercase;
}
